.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.grocery{
text-align: left;
}
.post{
  border-bottom:1px solid black ;
}
#title {
  text-align: center;
  font-family: arial, sans-serif;
}

#grocery {
  text-align: center;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  border: 3px solid #ddd;
  width: 100%;
}

#grocery td, #grocery th {
  border: 1px solid rgb(36, 240, 17);
  padding: 8px;
}

#grocery tr:nth-child(even){background-color: #f2f2f2;}

#grocery tr:hover {background-color: #ddd;}

#grocery th {
  padding-top: 2px;
  color: red;
}